.dashboard {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.responseCard {
    display: flex;
    flex-direction: column;
    font-size: 16px;
    align-items: center;
    gap: 30px;
    margin-top: 30px;
    background-color: #121321;
    border-radius: 10px;
    padding: 20px;
    width: 80%;
}

.responseCardSections {
    display: flex;
    flex-direction: row;
    width: 90%;
}

.responseBoxBoard {
    width: 50%;
    max-height: 700px;
}

.responseBoxBoardRight {
    width: 50%;
    max-height: 700px;
    overflow: hidden;
    overflow-y: scroll;
    padding-right: 15px;
}

.responseImage {
    max-height: 700px;
}

.titleInformationBox {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 20px;

}