.App {
  text-align: center;
  min-height: 100vh;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App body {
  width: 80%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.selectorProduct select{
  width: 400px;
  height: 50px;
  border-radius: 6px;
  text-align: center;
  font-size: 1.2rem;
}

.App span {
  font-size: 1.5rem;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  width: 70%;
}

.App-link {
  color: #61dafb;
}

.productImage {
  width: 90%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
