.rateBody {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 70%;
}

.rateUpper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 25px;
    margin: 25px;
}

.rateUpper input {
    height: 45px;
    width: 300px;
    font-size: 1.2rem;
    text-align: center;
    border-radius: 6px 0px 0px 6px;
}

.rateResponse {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.rateResponseImage {
    width: 60%;
    margin: 10px;
}

.rateResponseText {
    width: 40%;
    margin: 10px;
    overflow-y: auto;
    height: 75vh;
}

.criteriaList {
    display: flex;
    flex-direction: row;
    gap: 10px;
    flex-wrap: wrap;
    padding: 0;
    width: 60%;
}

.criteriaItem {
    margin: 5px 0;
    padding: 10px;
    border: 1px solid grey; /* Default border color */
    cursor: pointer;
    font-size: 1rem;
    border-radius: 6px;
}

.criteriaItem.selected {
    border-color: rgb(82, 115, 153); /* Border color when selected */
}

.enterButton {
    height: 49.2px;
    border-radius: 0px 6px 6px 0px;
    font-size: 1.2rem;
    text-align: center;
}

.enterButton:hover {
    background-color: rgb(80, 99, 133);
}

.responseBox {
    border: 1px solid;
    margin: 20px;
    padding: 5px;
    border-radius: 6px;
    font-size: 1.2rem;
    background-color: #2e3e50;
}

/* Add this to your CSS file */
.scoreStyle {
    font-weight: bold;
    color: #007bff; /* Example color */
}

